<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <div
          class="icon"
          v-for="item of page"
          :key="item.id"
        >
          <div class='icon-img' @click="backClick">
            <img class='icon-img-content' :src='item.imgUrl' />
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
    <transition name="fa">
      <div class="box1" v-if="tran"></div>
    </transition>
    <transition name="de">
          <div class="box2" v-if="tran">
            <p>该入口尚未开发,您可以：</p>
            <p>1 左右滑动试试</p>
            <p>2 查看“城市”页</p>
            <p>3 在“热销推荐”中订票</p>
            <p class="fontC">{{i}}</p></div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      },
      i: 4,
      tran: false
    }
  },
  methods: {
    backClick () {
      var this_=this;
      this_.i = 4;
      this_.tran=true;
      //禁止滚动
      // document.body.addEventListener('touchmove', function(e){
      //   e.preventDefault();
      //   e.stopPropagation();
      //   console.log(e);
      // }, { passive: false });
      document.body.style.position="fixed";
      document.body.style.width="100%";
      document.body.style.height="100%";
      var t = window.setInterval(function(){
        this_.i--;
      },1000);
      var t1 = window.setTimeout(function () {
        window.clearInterval(t);
        window.clearTimeout(t1); 
        this_.tran=false;
        document.body.style.position="";
      }, 3800);
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .box1
    position: absolute
    width: 100%
    height:100%
    opacity: 0.5
    background:#000
    z-index:9998
    top:0
    transition: opacity .5s
  .box2
    width:200px
    height:120px
    padding: 20px 0
    background:#fff
    position: absolute
    left:50%
    top:50%
    margin-top: -50px
    margin-left: -100px
    z-index:9999
    text-align:center
    line-height:25px
    opacity:1
    transition: opacity .5s
    border-radius: 10px
  .fa-enter, .fa-leave-to
    opacity: 0
  .de-enter, .de-leave-to
    opacity: 0
  .fontC
    color: $bgColor
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    margin-top: .1rem
    .icon
      position: relative
      overflow: hidden
      float: left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: .44rem
        box-sizing: border-box
        padding: .1rem
        .icon-img-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
        position: absolute
        left: 0
        right: 0
        bottom: 0
        height: .44rem
        line-height: .44rem
        text-align: center
        color: $darkTextColor
        ellipsis()
</style>



