<template>
  <div>
    <div
      class="item"
      v-for="(item, index) of list"
      :key="index"
    >
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-if="item.children" class="item-chilren">
        <!-- <detail-list :list="item.children"></detail-list> -->
        <div class="onen">{{item.children}}</div>
        <div class="onen-a"><a class="font-a">￥100</a> <span style="font-weight:bold"><a class="font-b" @click="handleBannerClick"> | 预订</a></span></div>
      </div>
      <!-- <div class="backone">
        <span>123</span>
      </div> -->
    </div>
    <fadeone>
      <common-payone
      v-show="showPayone"
      @close="handlePayClose"
      >
      </common-payone>
    </fadeone>
  </div>
</template>

<script>
import CommonPayone from 'common/gallary/Payone'
import Fadeone from 'common/fade/Fadeone'
export default {
  name: 'DetailList',
  components:{
    CommonPayone,
    Fadeone
  },
  props: {
    list: Array
  },
  data () {
    return {
      showPayone: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showPayone = true
      this.$emit('cssone')
    },
    handlePayClose () {
      this.showPayone = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .item-title-icon
    position: relative
    left: .06rem
    top: .06rem
    display: inline-block
    width: .36rem
    height: .36rem
    background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
    margin-right: .1rem
    background-size: .4rem 3rem
  .item-title
    line-height: .8rem
    font-size: .32rem
    padding: 0 .2rem
  // .item-chilren
  //   padding: 0 .2rem
  .item-chilren
    
    height: 1.2rem
    background: #f7f7f7
    .onen
      width: 4.7rem
      height: 0.6rem
      line-height: 0.318rem
      // background: #555
      padding: 0.3rem 0 0 0.3rem
      font-size: 13px
      float: left
  .onen-a
    height: 0.6rem;
    line-height: 0.318rem;
    padding: 0.45rem 0.32rem 0 0;
    float: right;
    .font-a
      font-size: 16px
      color: #ff4f0a
    .font-b
      color: #ff8f0a 
      // display: inline-block
      // width: 0.75rem
      // height: 0.5rem
      // background: #000
      // padding: 0rem 0 0 0 0      
</style>



