<!-- 头部 -->
<template>
  <div>
    <div class="header">
      <div class="header-left">
        <router-link :to="'/detail/0001'">
          <div class="iconfont back-icon color-font">&#xe624;</div>
        </router-link>
      </div>
      <div class="header-input">
        订单填写
      </div>
    </div>
    <div class="gela">
    </div>
    <div class="btia">
      <div class="btia-a">【上午场】故宫成人票凭身份证快速入园（淡季）</div>
      <div class="btia-b"><em>￥150</em><br/> /张</div>
    </div>
    <div class="gela">
    </div>
    <div class="bgie">
      <div class="bgie-a">
        <label class="mpw-row-field">游客1</label>
        <div class="">
          <!-- <label class="">游客0姓名</label> -->
          <input value="" class="mpw-input" type="text" placeholder="游客姓名" v-model="vname">
        </div>
      </div>
      <div class="bgie-a">
        <label class="mpw-row-field">手机号</label>
        <div class="">
          <!-- <label class="">游客0姓名</label> -->
          <input value="" class="mpw-input" type="text" placeholder="请输入手机号" v-model="vphone">
        </div>
      </div>
      <div class="bgie-a">
        <label class="mpw-row-field">身份证</label>
        <div class="">
          <!-- <label class="">游客0姓名</label> -->
          <input value="" class="mpw-input" type="text" placeholder="请填写正确的身份证号" v-model="vid">
        </div>
      </div>
      <div class="bgie-a">
        <label class="mpw-row-field">票数</label>
        <div class="">
          <!-- <label class="">游客0姓名</label> -->
          <input value="" class="mpw-input" type="text" placeholder="默认 1 张" v-model="vnum">
        </div>
      </div>
    </div>
    <div class="gela-a">
    </div>
    <div class="pay-bottom">
      <span class="one" @click="backClick"><a>立即预订</a></span>
    </div>
    <transition name="fa">
      <div class="box1" v-if="tran"></div>
    </transition>
    <transition name="de">
          <div class="box2" v-if="tran">
            <p>{{ptext1}}</p>
            <p>{{ptext2}}</p>
            <p class="fontC">{{i}}</p></div>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  },
  data () {
    return {
      vname: "",
      vphone: "",
      vid: "",
      vnum: "",
      ptext1: "",
      ptext2: "",
      i: 4,
      tran: false
    }
  },
  methods: {
    backClick () {
      var this_=this;
      if(this.vname!=""&&this.vphone!=""&&this.vid!=""&&this.vnum!=""){
        
        this.ptext1="预订信息已提交,您可以：";
        this.ptext2="继续预订或返回。"
      }else {
        this.ptext1="您的信息未填写完整";
        this.ptext2="请填写完整后再提交预订"
      }
      this.i = 4;
      this.tran=true;
      //禁止滚动
        document.body.addEventListener('touchmove', function(e){
          e.preventDefault();
          e.stopPropagation();
        }, { passive: false });
        var t = window.setInterval(function(){
          this_.i--;
        },1000);
        var t1 = window.setTimeout(function () {
          window.clearInterval(t);
          window.clearTimeout(t1); 
          this_.tran=false;
        }, 3800);
      
    }
  },
}
</script>

<style lang="stylus" scoped>
  // 1rem = html font-size = 50px
  @import '~styles/varibles.styl'
  .box1
    position: absolute
    width: 100%
    height:100%
    opacity: 0.5
    background:#000
    z-index:9998
    top:0
    transition: opacity .5s
  .box2
    width:200px
    height:60px
    padding: 20px 0
    background:#fff
    position: absolute
    left:50%
    top:50%
    margin-top: -45px
    margin-left: -100px
    z-index:9999
    text-align:center
    line-height:25px
    opacity:1
    transition: opacity .5s
    border-radius: 10px
  .fa-enter, .fa-leave-to
    opacity: 0
  .de-enter, .de-leave-to
    opacity: 0
  .fontC
    color: $bgColor
  .header
    display: flex
    line-height: $headerHeight
    background: $bgColor
    color: #fff
    .header-left
      width: .64rem
      float: left
      .back-icon
        text-align: center
        font-size: .4rem
    .header-input
      flex: 1
      height: .64rem
      line-height: .64rem
      margin-top: .12rem
      margin-right: .64rem
      padding-left: .2rem
      background: $bgColor
      border-radius: .1rem
      color: #fff
      text-align: center
  .gela
    height: .3rem
    background: #f4f4f4
  .gela-a
    height: 4.6rem
    background: #f4f4f4
  .btia
    height: 1.5rem
    border-bottom: 1px solid #dddddd
    padding: .3rem
    position: relative
    .btia-a
      width:75%
      font-size: 18px
      line-height: 24px
      
    .btia-b
      width: 20%
      position: absolute
      top: .6rem
      right: .2rem
      font-size: 16px
      line-height: 22px
      text-align: center
      em
        font-size:18px
        color: #ff4f0a
  .bgie
    height: 4rem
    .bgie-a
      height: .985rem
      border-bottom: 1px solid #dfdfde
      .mpw-row-field
        width: 1.3rem
        color: #616161
        font-size: 17px
        padding-top: .36rem
        padding-left: .4rem
        float: left
      .mpw-input
        font-size :17px
        padding-top: .26rem
        padding-left: .8rem
  .one
    display: block
    width: 100%
      
    position: absolute
    bottom: 0
    background: #ff9800
    text-align: center
    font: normal .36rem/1rem Arial,"Microsoft Yahei","Helvetica Neue",Helvetica,sans-serif
    a
     color: #fff
  .color-font
    color: #fff
</style>



