<template>
  <div>
    <div class="payback" @click="handlePayClick">
    </div>
    <div class="pay-a">
    </div>
    <div class="pay-b">
      <span class="payspan-a">【上午场】故宫成人票凭身份证快速入园（淡季）</span>
      <span class="payspan-b">￥<em class="payspanem">100</em></span>
      <span>/张</span>
      <span class="day-ri">日期选择</span>
      <span class="day-jt" :style="styleObj1" @click="headDivClick1">今天</span>
      <span class="day-mt" :style="styleObj2" @click="headDivClick2">明天</span>
      <span class="day-ht" :style="styleObj3" @click="headDivClick3">后天</span>
      <router-link to='/pay'
      ><span class="one"><a>立即预订</a></span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonPayone',
  data () {
    return {
      styleObj1: {
          background: "",color:""
      },
      styleObj2: {
          background: "",color:""

      },
      styleObj3: {
          background: "",color:""

      }
    }
  },
  methods: {
    handlePayClick () {
      this.$emit('close');
      this.styleObj1.background="";
      this.styleObj2.background="";
      this.styleObj3.background="";
      this.styleObj1.color="";
      this.styleObj2.color="";
      this.styleObj3.color="";
    },
    headDivClick1 () {
      this.styleObj1.background = this.styleObj1.background === ""? "#00bcd4" : "";
      this.styleObj1.color = this.styleObj1.color === ""? "#fff" : "";
      if(this.styleObj3.background="#00bcd4",this.styleObj2.background="#00bcd4"){
        this.styleObj3.background="";
        this.styleObj3.color="";
        this.styleObj2.background="";
        this.styleObj2.color=""
      }
    },
    headDivClick2 () {
      this.styleObj2.background = this.styleObj2.background === ""? "#00bcd4" : "";
      this.styleObj2.color = this.styleObj2.color === ""? "#fff" : "";

      if(this.styleObj3.background="#00bcd4",this.styleObj1.background="#00bcd4"){
        this.styleObj3.background="";
        this.styleObj3.color="";
        this.styleObj1.background="";
        this.styleObj1.color=""
      }
    },
    headDivClick3 () {
      this.styleObj3.background = this.styleObj3.background === ""? "#00bcd4" : "";
      this.styleObj3.color = this.styleObj3.color === ""? "#fff" : "";

      if(this.styleObj1.background="#00bcd4",this.styleObj2.background="#00bcd4"){
        this.styleObj1.background="";
        this.styleObj1.color="";
        this.styleObj2.background="";
        this.styleObj2.color=""
      }
    }
  }
}

</script>

<style lang="stylus" scoped>
  *
    transition: all .1s
    -webkit-transition: all .1s
    -ms-transition: all .1s
    -moz-transition: all .1s
  .payback
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom:0
    background: rgba(0 0 0 0.4)
  .pay-a
    position: fixed
    left: 0
    right: 0
    bottom: 0
    background: rgba(0 0 0 0.4)
    height: 4.86rem
  .pay-b
    position: fixed
    left: 0
    right: 0
    bottom: 0
    background: rgba(255 255 255 1)
    height: 4.84rem
    .payspan-a
      display: block
      padding: 0.4rem 0.5rem 0.2rem 0.3rem
      font-size: 18px
      line-height: 22px
    .payspan-b
      padding-left: 0.3rem
      color: #ff4f0a
      .payspanem
        font-size: 18px
    .day-ri
      display: block
      padding: 0.4rem 0 0 0.3rem
      font-size: 12px
    .day-jt
      display: inline-block
      margin-top: 0.2rem
      margin-left: 7.3%
      width: 24%
      height: 0.8rem
      line-height: 0.8rem
      text-align: center
      border: .02rem solid #bdbdbd
      border-radius: .1rem
      float: left
    .day-mt
      display: inline-block
      margin-top: 0.2rem
      margin-left: 6%
      width: 24%
      height: 0.8rem
      line-height: 0.8rem
      text-align: center
      border: .02rem solid #bdbdbd
      border-radius: .1rem
      float: left
    .day-ht
      display: inline-block
      margin-top: 0.2rem
      margin-left: 6%
      width: 24%
      height: 0.8rem
      line-height: 0.8rem
      text-align: center
      border: .02rem solid #bdbdbd
      border-radius: .1rem
    .one
      display: block
      width: 100%
      position: absolute
      bottom: 0
      background: #ff9800
      text-align: center
      font: normal .36rem/1rem Arial,"Microsoft Yahei","Helvetica Neue",Helvetica,sans-serif
      a
        color: #fff
</style>



